h1{
  text-align: center;
}
h2{
  text-align: center;
}
h3{
  text-align: center;
}

.app-wrapper{
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-self: center;
    align-items: center;
    border-radius: 2%;
    align-self: center;
    min-width: 700px;
    max-width: 800px;
    background: lightgrey;
    margin-top: 10%;
}

.main-title{
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
}

.main-category-list-wrapper{
  display: grid;
  grid-gap: 25px;
  grid-template-columns: 200px 200px;
}

.category-list-wrapper{
  display: grid;
  grid-gap: 25px;
  grid-template-columns: 200px 200px 200px;
}

:hover.category-card-wrapper{
  box-shadow: 0 0 15px 20px antiquewhite;
}

.category-card-wrapper{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-radius: 5%;
  width: 200px;
  height: 150px;
  background: grey;
  cursor: pointer;
}

input{
  height: 25px;
  width: 400px;
  border: none;
  background-color: antiquewhite;
  margin-bottom: 25px;
}

.btn{
  border: none;
  border-radius: 50%;
  height: 75px;
  width: 75px;
  box-shadow: 0 5px 5px 5px antiquewhite;
  cursor: pointer;
}

:hover.btn{
  box-shadow: 0px 0px 5px 15px antiquewhite;
  border: solid 2px grey;
}
.btn-green{
  background-color: lightgreen;
 
}

.exercises-list-wrapper{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.btns-wrapper{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.exercises-list{
  display: grid;
  grid-gap: 25px;
  grid-template-columns: 200px 200px 200px;
}

.exercise-card-wrapper{
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
background: grey;
cursor: pointer;
border-radius: 5%;
}
:hover.exercise-card-wrapper{
  box-shadow: 0 0 15px 20px antiquewhite;
}

.exercise-card-img{
width: 200px;
height: auto;
border-radius: 5%;
}
:hover.exercise-card-img{
  transform: scale(1.1);
  transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
}

.exercise-card-name{
text-align: center;
}

.new-exercise-adding-wrapper{
  flex-direction: column;
  display: flex;
  align-items: center;
}

.icon-btns-name-wrapper{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: baseline;
  align-self: center;
}

.edit{
  cursor: pointer;
  background: none;
  border: none;
}

.delete{
  cursor: pointer;
  background: none;
  border: none;
  color: black;
}

.modal-wrapper{
  height: 600px;
  width: 100%;
  background: rgba(128, 128, 128, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
  transform: scale(0.5);
  position: relative;
  border-radius: 12px;
}

.modal-wrapper.active{
  transform: scale(1);
  opacity: (1);
  pointer-events: all;
}

.modal-content{
  padding: 20px;
  border-radius: 12px;
  background-color: antiquewhite;
  opacity: 1;
  width: 400px;
  height: 500px;
}

.close-modal{
  display: none;
}

.paste-input{
  opacity: 0;
  position: absolute;
  z-index: -1;
}

.paste-image-wrapper{
  margin-top: 5px;
  cursor: pointer;
}

label{
  cursor: pointer;
  margin-bottom: 20px;
  background: antiquewhite;
}

#imgFile{
  display: none;
  background: antiquewhite;
  width: 300px;
}

.cycling-wrapper{
  
}

.weight-reps-inputs{
  display: flex;
  flex-direction: row;
  align-items: center;
}

.weight-input{
  width: 40px;
  margin-right: 5px;
}
.reps-input{
  width: 40px;
  margin-right: 5px;
}

.add-weight-reps-btn{
  margin-top: -25px;
}